import defaultPic from './default.gif';
import no1 from './No1.gif';
import no2 from './No2.gif';
import no3 from './No3.gif';
import no4 from './No4.gif';
import yesPic from './yes.gif';
import './App.css';
import React, { useState } from "react";

function App() {
  const [indexPicture, setIndexPicture] = useState(-1);

  const noPictures = [no1, no2, no3, no4];

  // https://codepen.io/pradeepth02/pen/LYXgMaP
  const hoverNo = (e) => {
    setIndexPicture((indexPicture + 1) % noPictures.length);
    const noButton = document.getElementById("no-btn");

    noButton.style.position = "absolute";
    noButton.style.left = Math.random() * 80 + "vw";
    noButton.style.top = Math.random() * 80 + "vh";
  };

  const clickYes = () => {
    setIndexPicture(-2);
    const noButton = document.getElementById("no-btn");
    noButton.style.display = "none";
    document.getElementById("textApp").innerHTML = "Kim, I am yours!&#x1F495;";
  };

  return (
    <div className="App-Container">
        <div><img src={indexPicture === -2 ? yesPic :  indexPicture === -1 ? defaultPic: noPictures[indexPicture]} className="App-logo" alt="error" /></div>
        <div><h1 id ="textApp">My Sunshine Kim,<br/>Can I be your boyfriend?</h1></div>
        <div className="buttons">
            <button id="yes-btn" onClick={clickYes}>Yes</button>
            <button id="no-btn" onMouseEnter={e => hoverNo(e)}>No</button>
        </div>
    </div>
  );
}

export default App;
